import axios from 'axios';

import { LOGIN_USER, REGISTER_USER, GET_PROFILE_DATA, USER_AUTH } from './actionTypes';

export async function loginUser({ email, password }) {
    const request = await axios({
        method: 'post',
        url: '/api/login',
        credentials: 'same-origin',
        data: { email, password }
    }).then(res => res.data)

    return {
        type: LOGIN_USER,
        payload: request
    }
}

export async function registerUser(user) {
    const request = await axios.post(`/api/register`, user)
    .then(data => data.data);

    return {
        type: REGISTER_USER,
        payload: request
    }
}

export function auth() {
	const request = axios.get('/api/auth')
	.then(res => res.data)

	return {
		type: USER_AUTH,
		payload: request
	}
}

export function getProfileData(id) {

    return {
        type: GET_PROFILE_DATA,
        payload: 1
    }
}
