import React from 'react';
import L from 'leaflet';

import './mapContainer.scss';
 
class MapContainer extends React.Component {
    componentDidMount() {
        this.map = L.map('map').setView([49.273201, 28.562994], 2);

        L.tileLayer('http://{s}.tile.osm.org/{z}/{x}/{y}.png', {
            attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
        }).addTo(this.map);
    }

    componentWillReceiveProps(nextProps) {
        if(nextProps.homes && nextProps.homes.homes && nextProps.homes.homes.length){
            
            const self = this;

            for(let home of nextProps.homes.homes) {
                let marker = L.marker([home.coords.lattitude, home.coords.longitude]).addTo(this.map);

                const actionClass = home.action.use ? "popup__item-action blue" : "popup__item-action";

                var popup = L.popup()
                .setLatLng([home.coords.lattitude, home.coords.longitude])
                .setContent(`
                    <div class="popup__item">
                    <div class="popup__item-image" style="background: url(`+ home.mainImageUrl + `);"}>
                        <div class=` + actionClass + ` >
                            <span>` + home.action.types + `</span>
                        </div>
                    </div>
                    <div class="popup__item-text">
                        <h4 class="popup__item-title">` + home.title + `</h4>
                        <h6 class="popup__item-description">` + home.adress +`</h6>
                        <div class="popup__item-bottom">
                            <div class="price-wrapper">
                                <div class="item-price">
                                    <span>$` + home.price + `</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div> 
                `)
                .openOn(this.map);
                marker.on('click', () => {
                    self.props.loadHomeById(home._id);
                    self.props.showHomeDetails();
                })
                marker.bindPopup(popup).openPopup();
                this.map.setView([49.273201, 28.562994], 2);
            }
        }
    }

    render() {
        return (
            <div id="map"></div>
        );
    }
}

export default MapContainer;
