import axios from 'axios';
import { LOAD_ALL_HOMES, LOAD_HOME_BY_ID, ADD_COMMENT, FILTER_HOMES, LOAD_MAIN_HOME } from './actionTypes';

export async function loadAllHomes() {
    const request = await axios.get('/api/homes')
    .then(res => res.data)

    return {
        type: LOAD_ALL_HOMES,
        payload: request
    }
}

export async function loadMainHome() {
    const request = await axios.get('/api/home/main')
    .then(res => res.data)

    return {
        type: LOAD_MAIN_HOME,
        payload: request
    }
}

export async function filterHomes(params) {
    const request = await axios.get(`/api/homes/filter?path=${params}`)
    .then(res => res.data)

    return {
        type: FILTER_HOMES,
        payload: request
    }
}

export async function loadHomeById(id) {
    const request = await axios.get(`/api/home/${id}`)
    .then(res => res.data)

    return {
        type: LOAD_HOME_BY_ID,
        payload: request
    }
}

export async function addComment( id, comment) {
    const request = await axios.post(`/api/home/${id}/comment`, { comment })
    .then(res => res.data)

    return {
        type: ADD_COMMENT,
        payload: {
            comment,
            request
        }
    }
}
