import React from 'react';
import { Link } from 'react-router-dom';
import FormField from '../widgets/FormFields';
import './Register.scss';

import { validate } from '../utils';

import { validForm, firstQuestion, secondQuestion, login, register, registerB} from './strings';

class Register extends React.Component {
    constructor(props){
        super(props);

        this.state = {
            registerError: '',
            loading: false,
            formData: {
                name: {
                    element: 'input',
                    value: '',
                    config: {
                        name: 'name_input',
                        type: 'text',
                        className: 'register-input register-input__name',
                        placeholder: 'Your name'
                    },
                    validation: {
                        required: true,
                        name: true
                    },
                    valid: false,
                    touched: false,
                    validationMessage: ''
                },
                lastname: {
                    element: 'input',
                    value: '',
                    config: {
                        name: 'lastname_input',
                        type: 'text',
                        className: 'register-input register-input__lastname',
                        placeholder: 'Yout lastname'
                    },
                    validation: {
                        required: true,
                        name: true
                    },
                    valid: false,
                    touched: false,
                    validationMessage: ''
                },
                email: {
                    element: 'input',
                    value: '',
                    config: {
                        name: 'email_input',
                        type: 'email',
                        className: 'register-input register-input__email',
                        placeholder: 'Your email address'
                    },
                    validation: {
                        required: true,
                        email: true
                    },
                    valid: false,
                    touched: false,
                    validationMessage: ''
                },
                password: {
                    element: 'input',
                    value: '',
                    config: {
                        name: 'password_input',
                        type: 'password',
                        className: 'register-input register-input__password',
                        placeholder: 'Password'
                    },
                    validation: {
                        required: true,
                        password: true
                    },
                    valid: false,
                    touched: false,
                    validationMessage: ''
                }
            }
        };
    }

    updateForm = (elem) => {
        const newFormData = {
            ...this.state.formData
        }
        const newElem = {
            ...newFormData[elem.id]
        }
        newElem.value = elem.e.target.value;
        if(elem.blur){
            let validData = validate(newElem);
            newElem.valid = validData[0];
            newElem.validationMessage = validData[1]
        }

        newElem.touched = elem.blur;
        newFormData[elem.id] = newElem;

        this.setState({
            formData: newFormData
        })
    }


    submitForm = (e, type) => {
        e.preventDefault();
        if(type !== null){
            let dataToSubmit = {};
            let formIsValid = true;

            for(let key in this.state.formData){
                dataToSubmit[key] = this.state.formData[key].value
            }
            for(let key in this.state.formData){
                formIsValid = this.state.formData[key].valid && formIsValid;
            }

            if(formIsValid){
                this.setState({
                loading: true,
                registerError: ''
            })

                this.props.registerUser(dataToSubmit);

            } else {
                this.setState({
                    registerError: validForm
                })
            }
        }
    }

    componentWillReceiveProps(nextProps){
        if(!nextProps.log && nextProps.log.registerUser && nextProps.log.registerUser.success){
            this.setState({
                loading: false,
                registerError: 'Error! Try again.'
            })
        } else if (nextProps.log && nextProps.log.registerUser && nextProps.log.registerUser.user) {
            this.props.history.push('/login');
        }
    }

    showLoader = () => (
        this.state.loading ?
            <div className="loader__wrapper">
                <div className="loader"><div></div><div></div></div>
            </div>
        : null
    )

    showError = () => (
        this.state.registerError !== '' ?
            <span className="register-block__error">
                {this.state.registerError}
            </span>
        : null
    )


    render(){
        return(
            <div className="register-wrapper">
                <div className="register-wrapper__inline">
                    {this.showLoader()}
                    <div className="register-block__top">
                        <h2 className="register-block__title">{register}</h2>
                        <form className="register-block__form" onSubmit={(e) => this.submitForm(e, null)}>
                            <FormField
                                id={'name'}
                                formData={this.state.formData.name}
                                change={(elem) => this.updateForm(elem)}
                            />
                            <FormField
                                id={'lastname'}
                                formData={this.state.formData.lastname}
                                change={(elem) => this.updateForm(elem)}
                            />
                            <FormField
                                id={'email'}
                                formData={this.state.formData.email}
                                change={(elem) => this.updateForm(elem)}
                            />
                            <FormField
                                id={'password'}
                                formData={this.state.formData.password}
                                change={(elem) => this.updateForm(elem)}
                            />
                            <div className="register-block__container">
                                <button className="register-block__submit" onClick={(e) => this.submitForm(e, true)}>{registerB}</button>
                                {this.showError()}
                            </div>
                        </form>
                    </div>
                    <div className="register-block__bottom">
                        <div className="register-bottom__wrapper">
                            <div className="register-bottom__link">
                                <p>{firstQuestion}</p>
                                <Link className="register-bottom__register" to="/login">{login}</Link>
                            </div>
                            <p>{secondQuestion}</p>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Register;
