import React from 'react';
import Rating from '../Rating';
import Icons from '../widgets/Icons';
import Comment from '../Comment';
import { Slide } from 'react-slideshow-image';

import './homeDetails.scss';

class HomeDetails extends React.Component {
    state = {
        comment: ''
    }

    setComment = (e) => {
        this.setState({
            comment: e.target.value
        })
    }

    addComment = () => {
        this.props.addComment(this.props.home._id, this.state.comment);

        this.setState({
            comment: ''
        });
    }

    renderComments = () => {
        const comments = this.props.home.comments.reverse();

        return comments.map((comment, index) => (
            <Comment comment={comment} key={index} />
        ))
    }

    render() {
        const properties = {
            duration: 4000,
            transitionDuration: 500,
            infinite: true,
            indicators: true,
            arrows: false
        }

        return (
            <div className="home-details">
                <div className="home-details__main">
                    <h3 className="home-details__main-title">{this.props.home.title}</h3>
                    <p className="home-details__main-description">{this.props.home.adress}</p>
                    <div className="home-details__close" onClick={() => this.props.hideHomeDetails()}>
                        <Icons 
                            icon='close' 
                            size="20px"
                            color="#4ea79a"
                        />
                    </div>
                </div>
                <div className="home-details__assessment">
                    <div className="home-details__rating">
                        <Rating
                            rating={this.props.home.rating}
                            dimension="15px"
                            spacing="2px"
                        />  
                    </div>
                    <div className="home-details__likes">
                        <Icons 
                            icon='like' 
                            size="13px"
                            color="red"
                        />
                        <span>{this.props.home.likes} loved this</span>
                    </div>
                </div>
                <div className="home-details__slider">
                    <Slide {...properties}>
                        {
                            this.props.home.sliderImages.map((image, index) => (
                                <div key={index} className="home-details__slider-slide">
                                    <div className="home-details__slider-image" style={{'backgroundImage': `url(${image})`}}></div>
                                </div>
                            ))
                        }
                    </Slide>
                    <div className="home-details__price">
                        <span>${this.props.home.price}</span>
                    </div>
                    <h3 className="home-details__slider-title">{this.props.home.title}</h3>
                </div>
                <div className="home-details__rooms">
                    <div className="home-details__rooms-item">
                        <Icons 
                            icon='home' 
                            size="13px"
                            color="#404040"
                        />
                        <span>House</span>
                    </div>
                    <div className="home-details__rooms-item">
                        <Icons 
                            icon='bedrooms' 
                            size="13px"
                            color="#404040"
                        />
                        <span>{this.props.home.rooms.bedrooms} Bedrooms</span>
                    </div>
                    <div className="home-details__rooms-item">
                        <Icons 
                            icon='bathrooms' 
                            size="13px"
                            color="#404040"
                        />
                        <span>{this.props.home.rooms.bathrooms} Bathrooms</span>
                    </div>
                </div>
                <div className="home-details__section">
                    <h3 className="home-details__section-title">Description</h3>
                    <div className="home-details__section-wrapper">
                        <p>{this.props.home.description}</p>
                        <div className="home-details__agent">
                            <div className="home-details__agent-image"></div>
                            <h4 className="home-details__agent-title">Michelle D.</h4>
                            <button className="home-details__agent-button">Contact Agent</button>
                        </div>
                    </div>
                </div>
                <div className="home-details__section">
                    <h3 className="home-details__section-title">Comments</h3>
                    <div className="home-details__section-wrapper comments">
                        <input type="text" value={this.state.comment} className="home-details__comments-add" onChange={(e) => this.setComment(e)}/>
                        <div className="home-details__comments-button" onClick={() => this.addComment()}>
                            <Icons 
                                icon='add'
                                size="12px"
                                color="#fff"
                            /> 
                        </div>
                    </div>
                    <div className="home-details__comments">
                        {this.renderComments()}
                    </div>
                </div>
            </div>
        )
    }
}

export default HomeDetails;