import React from 'react';
import { Link } from 'react-router-dom';
import FormField from '../widgets/FormFields';
import './Login.scss';
import { validate } from '../utils';

import { validForm, firstQuestion, secondQuestion,register } from './strings';

class Login extends React.Component {
   state = {
        loginError: '',
        loading: false,
        formData: {
            email: {
                element: 'input',
                value: '',
                config: {
                    name: 'email_input',
                    type: 'email',
                    className: 'login-input login-input__email',
                    placeholder: 'Your email address'
                },
                validation: {
                    required: true,
                    email: true
                },
                valid: false,
                touched: false,
                validationMessage: ''
            },
            password: {
                element: 'input',
                value: '',
                config: {
                    name: 'password_input',
                    type: 'password',
                    className: 'login-input login-input__password',
                    placeholder: 'Password'
                },
                validation: {
                    required: true,
                    password: true
                },
                valid: false,
                touched: false,
                validationMessage: ''
            }
        }
    }


    updateForm = (elem) => {
        const newFormData = {
            ...this.state.formData
        }
        const newElem = {
            ...newFormData[elem.id]
        }
        newElem.value = elem.e.target.value;
        if(elem.blur){ 
            let validData = validate(newElem);
            newElem.valid = validData[0];
            newElem.validationMessage = validData[1]
        }

        newElem.touched = elem.blur;
        newFormData[elem.id] = newElem;

        this.setState({
            formData: newFormData
        })
    }


    submitForm = (e, type) => {
        e.preventDefault();

        let dataToSubmit = {};
        let formIsValid = true;

        for(let key in this.state.formData){
            dataToSubmit[key] = this.state.formData[key].value
        }
        for(let key in this.state.formData){
            formIsValid = this.state.formData[key].valid && formIsValid;
        }

        if(formIsValid){
            this.setState({
                loading: true,
                loginError: ''
            })

            this.props.loginUser(dataToSubmit);

        } else {
            this.setState({
                loginError: validForm
            })
        }
    }

    componentDidMount() {
        if (this.props.log.registerUser && this.props.log.registerUser.success) {
            const newFormData = {
                ...this.state.formData
            }
            newFormData.email.value = this.props.log.registerUser.user.email;
            newFormData.email.valid = true;

            this.setState({
                formData: newFormData
            })
        }
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.log && nextProps.log.login && !nextProps.log.login.isAuth
            && nextProps.log.login.message
        ){
            this.setState({
                loading: false,
                loginError: nextProps.log.login.message
            });
        } else if (nextProps.log && nextProps.log.login && nextProps.log.login.isAuth 
            && nextProps.log.login.id) 
        {
            this.setState({
                loading: false
            });

            this.props.history.push('/dashboard');
        }
    }

    showLoader = () => (
        this.state.loading ?
            <div className="loader__wrapper">
                <div className="loader"><div></div><div></div></div>
            </div>
        : null
    )

    showError = () => (
        this.state.loginError !== '' ?
            <span className="login-block__error">
                {this.state.loginError}
            </span>
        : null
    )


    render(){
        return(
            <div className="login-wrapper">
                <div className="login-wrapper__inline">
                    {this.showLoader()}
                    <div className="login-block__top">
                        <h2 className="login-block__title">Login in Real Estate</h2>
                        <form className="login-block__form" onSubmit={(e) => this.submitForm(e)}>
                            <FormField
                                id={'email'}
                                formData={this.state.formData.email}
                                change={(elem) => this.updateForm(elem)}
                            />
                            <FormField
                                id={'password'}
                                formData={this.state.formData.password}
                                change={(elem) => this.updateForm(elem)}
                            />
                            <div className="login-block__container">
                                <button className="login-block__submit" onClick={(e) => this.submitForm(e)}>Войти</button>
                                {this.showError()}
                            </div>
                        </form>
                    </div>
                    <div className="login-block__bottom">
                        <div className="login-bottom__wrapper">
                            <div className="login-bottom__link">
                                <p>{firstQuestion}</p>
                                <Link className="login-bottom__register" to="/register">{register}</Link>
                            </div>
                            <p>{secondQuestion}</p>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Login;
