import { LOGIN_USER, REGISTER_USER, GET_PROFILE_DATA, USER_AUTH } from '../actions/actionTypes';

export function log(state = {}, action) {
    switch (action.type) {
        case GET_PROFILE_DATA:
            return { ...state, login: action.payload };
        case LOGIN_USER:
            return { ...state, login: action.payload };
        case USER_AUTH:
            return { ...state, login: action.payload }
        case REGISTER_USER:
            return { ...state, registerUser: action.payload };
        default:
            return state;
    }
}
