import { LOAD_ALL_HOMES, LOAD_HOME_BY_ID, ADD_COMMENT, FILTER_HOMES, LOAD_MAIN_HOME } from '../actions/actionTypes';
  
const initialState = {
    loadHomes: {
        homes: [],
        success: false
    },
    loadSomeHome: {
        home: {},
        success: false
    }
}

export function homes(state = initialState, action) {
    switch (action.type) {
        case LOAD_ALL_HOMES:
            return { ...state, loadHomes: action.payload }
        case LOAD_MAIN_HOME:
            return { ...state, mainHome: action.payload }
        case LOAD_HOME_BY_ID:
            return { ...state, loadSomeHome: action.payload }
        case FILTER_HOMES:
            return { ...state, loadHomes: action.payload }
        case ADD_COMMENT:
            const newHomes = state.loadHomes.homes;
            const newHome = state.loadSomeHome.home;
            
            newHomes.forEach((it, index) => {
                if(it.id === action.payload.request.home.id) {
                    newHomes[index].comments.unshift(action.payload.comment);
                }
            });

            newHome.comments.unshift(action.payload.comment);

            return { ...state, 
                loadHomes: { homes: newHomes, success: true },
                loadSomeHome: { home : newHome, success: true}
            }
        default:
            return state;
    }
}
