import React, { Component } from 'react';
import Rating from '../Rating';
import HomeListItem from '../HomeListItem';
import Icons from '../widgets/Icons';
import HomeDetails from '../HomeDetails';

import './dashboardContent.scss';

class DashboardContent extends Component {

    renderHomeList = () => {
        const homes = this.props.homes.homes;

        return !!homes ? homes.map((home, index) => (
            <HomeListItem
                key={index}
                showHomeDetails={this.showHomeDetails}
                home={home}
            />
        )) : (
            <h4 className="homes-list__not-found">Homes not found</h4>
        );
    }

    showHomeDetails = (id) => {
        if (!id) return;

        this.props.loadHomeById(id);
    }

    renderContent = () => {
        const mainHome = this.props.mainHome;

        return this.props.loadSomeHome.success && this.props.showHomeDetails ? (
            <div className="main-content">
                <HomeDetails
                    home={this.props.loadSomeHome.home}
                    addComment={this.props.addComment}
                    hideHomeDetails={this.props.hideHomeDetails}
                />
            </div>
        ) : (
            <div className="main-content">
                <div className="main-content__preview" style={{ background: `url(${mainHome ? mainHome.mainImageUrl : ''})`}}>
                    <div className="main-content__preview-wrapper">
                        <h2 className="main-content__preview-title">{mainHome ? mainHome.title : ''}</h2>
                        <h4 className="main-content__preview-description">{mainHome ? mainHome.adress : 0}</h4>
                        <Rating
                            rating={mainHome ? mainHome.rating : 0}
                            dimension="20px"
                            spacing="4px"
                        />  
                    </div>
                    <div className="main-content__preview-button" onClick={() => this.showHomeDetails(mainHome ? mainHome._id : null)}>
                        <Icons 
                            icon='star' 
                            size="18px" 
                            color="#fff"
                        />
                        <span>Details</span>
                    </div>
                </div>
                <div className="homes">
                    <h4 className="homes__title">Properties</h4>
                    <ul className="homes-list">
                        {this.renderHomeList()}
                    </ul>
                </div>
            </div>
        )
    }

    render() {
        return (
            <div className="main-content__wrapper">
               {this.renderContent()}
            </div>
        )
    }
}

export default DashboardContent;
