import { connect } from 'react-redux';
import Dashboard from '../components/Dashboard';
import { loadAllHomes, loadHomeById, addComment, filterHomes, loadMainHome } from '../app/actions/homes';


function mapDispatchToProps(dispatch){
    return {
        loadAllHomes: () => dispatch(loadAllHomes()),
        loadMainHome: () => dispatch(loadMainHome()),
        loadHomeById: (id) => dispatch(loadHomeById(id)),
        addComment: (id, comment) => dispatch(addComment(id, comment)),
        filterHomes: (params) => dispatch(filterHomes(params))
    }
}

const mapStateToProps = (state) => {
    return {
        homes: state.homes
	}
};

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);