import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import axios from 'axios';
import './header.scss';
import Icons from '../widgets/Icons';

class Header extends React.Component {
    state = {
        showUserConfig: false
    }
    
    componentDidMount(){
        document.addEventListener("click", this.documentClickHandler);
    } 
    
    componentWillUnmount() {
        document.removeEventListener("click", this.documentClickHandler);
    }

    documentClickHandler = () => {
        this.setState({
            showUserConfig: false
        });
    }

    dropdownClickHandler = (e) => {
        e.nativeEvent.stopImmediatePropagation();
    }

    toggleUserConfig = () => {
        this.setState({
            showUserConfig: !this.state.showUserConfig
        })
    }

    logout = async () => {
        const status = await axios.get(`/api/logout`)
        .then(request => request.status)

        if (status === 200) {
            this.props.history.push('/')
        }
    };

 
    showUserConfig = () => (
        this.state.showUserConfig ?
            <div className="user-config__list">
                <div className="user-config__info">
                    <span>@{this.props.log.login.name}</span>
                </div>
                <div className="logout-wrapper" onClick={() => this.logout()} >
                    <Icons
                        icon='logout' 
                        size="20px" 
                        color='#000'
                        className='outline'
                        style={{margin: '0 12px 0 0'}}
                    />
                    <span>Выйти</span>
                </div>
            </div>
        : null
    )

    showHeaderTemplate = (isAuth) => {
        return isAuth ? (
            <div className="container">
                <div className="main-logo" >
                    <Link to="/" className="main-logo__link">
                        <Icons 
                            icon='homeReal' 
                            size="15px" 
                            color="#4ea79a"
                            style={{margin: '0 5px 0 0'}}
                        /> 
                        <span>REAL ESTATE</span>
                    </Link>
                </div>
                <div className="nav-right">
                    <h3>{this.props.log.login.name} {this.props.log.login.lastname}</h3>
                    <div className="user-config" onClick={this.dropdownClickHandler}>
                        <Icons 
                            icon='user' 
                            size="32px" 
                            handleIcon={this.toggleUserConfig}
                            style={{cursor: 'pointer'}}
                        />
                        {this.showUserConfig()}
                    </div>
                </div>
            </div>
        ) : (
            <div className="container" style={{padding: "7px 0"}}>
                <div className="main-logo" >
                    <Link to="/" className="main-logo__link">
                        <Icons 
                            icon='homeReal'
                            size="15px"
                            color="#4ea79a"
                            style={{margin: '0 5px 0 0'}}
                        /> 
                        <span>REAL ESTATE</span>
                    </Link>
                </div>
            </div>
        )
    }

    render(){
        return(
            <header>
                {this.showHeaderTemplate(this.props.log && this.props.log.login && this.props.log.login.isAuth)}
            </header>
        )
    }
}

export default withRouter(Header);