import React from 'react';
import Rating from '../Rating';
import Icons from '../widgets/Icons';

import './homeListItem.scss';

const HomeListItem = (props) => {

    return (
        <li className="homes-list__item">
            <div className="homes-list__item-image" style={{ background: `url(${props.home.mainImageUrl})`}}>
                <div className={props.home.action.use ? "homes-list__item-action blue" : "homes-list__item-action"} >
                    <span>{props.home.action.types}</span>
                </div>
                <div className="homes-list__item-wrapper"></div>
                <div className="homes-list__item-view" onClick={() => props.showHomeDetails(props.home._id)}>
                    <Icons 
                        icon='eye'
                        size="15px" 
                        color="#000"
                    />
                </div>
            </div>
            <div className="homes-list__item-text">
                <h4 className="homes-list__item-title">{props.home.title}</h4>
                <div className="homes-list__item-rating">
                    <Rating
                        rating={props.home.rating}
                        dimension="12px"
                        spacing="2px"
                    />  
                </div>
                <h6 className="homes-list__item-description">{props.home.adress}</h6>
                <div className="homes-list__item-bottom">
                    <div className="price-wrapper">
                        <div className="item-price">
                            <span>${props.home.price}</span>
                        </div>
                        <Icons 
                            icon='home' 
                            size="13px" 
                            color="#000"
                        />
                    </div>
                    <div className="item-basket" onClick={() => props.showHomeDetails(props.home._id)}>
                        <Icons 
                            icon='basket' 
                            size="18px" 
                            color="#fff"
                        />
                    </div>
                </div>
            </div>
        </li>
    )
}

export default HomeListItem;