import React from 'react';
import MapContainer from '../../containers/mapContainer';
import Icons from '../widgets/Icons';
import DashboardContent from '../DashboardContent';

import './dashboard.scss';

class Dashboard extends React.Component {
    state = {
        showDashboard: false,
        comment: '',
        searchPath: '',
        showHomeDetails: true
    }

    componentDidMount() {
        this.props.loadAllHomes();
        this.props.loadMainHome();
    }

    hideHomeDetails = () => {
        this.setState({
            showHomeDetails: false
        })
    }

    showHomeDetails = () => {
        this.setState({
            showHomeDetails: true
        })
    }

    showDashboard = () => {
        this.setState({
            showDashboard: !this.state.showDashboard
        })
    }

    setSearch = (e) => {
        this.setState({
            searchPath: e.target.value
        })

        if(!e.target.value) {
            this.startSearch('')
        }
    }

    startSearch = (params) => {
        this.props.filterHomes(params);

        this.setState({
            showHomeDetails: false
        });
    }

    loadHomeById = (id) => {
        this.props.loadHomeById(id);

        this.setState({
            showHomeDetails: true
        });
    }

    addComment = (id, comment) => {
        this.props.addComment(id, comment);
    }

    dashboardRender = () => (
        <div className={ this.state.showDashboard ? 'dashboard-content hide' : 'dashboard-content'}>
            <DashboardContent
                homes={this.props.homes.loadHomes}
                mainHome={this.props.homes.mainHome}
                loadSomeHome={this.props.homes.loadSomeHome}
                loadHomeById={this.loadHomeById}
                addComment={this.addComment}
                showHomeDetails={this.state.showHomeDetails}
                hideHomeDetails={this.hideHomeDetails}
            />
        </div>
    )

  	render() {
		return(
			<div className="dashboard">
                <div className="dashboard-header">
                    <div className="left-block">
                        <div className="description">
                            <Icons 
                                icon='info'
                                size="12px"
                                color="#fff"
                                style={{ margin: '0 5px 0 0' }}
                            /> 
                            <h4>Discover thousands of locations</h4>
                        </div>
                        <div className="search">
                            <input type="text" placeholder="Search" onChange={(e) => this.setSearch(e)}/>
                            <div className="search-button" onClick={() => this.startSearch(this.state.searchPath)}>
                                <Icons 
                                    icon='search'
                                    size="15px"
                                    color="#fff"
                                /> 
                            </div>
                        </div>
                    </div>
                    <div className="burger-wrapper" onClick={() => this.showDashboard()}>
                        <Icons 
                            icon='burger'
                            size="30px"
                            color="#fff"
                        /> 
                    </div>
                </div>
                <div className="dashboard-wrapper">
                    <MapContainer
                        homes={this.props.homes.loadHomes}
                        showHomeDetails={this.showHomeDetails}
                    />
                    <div className={ this.state.showDashboard ? 'dashboard-content__hide hide' : 'dashboard-content__hide'} 
                        onClick={() => this.showDashboard()}>
                        <Icons 
                            icon='dots'
                            size="16px"
                            color="#fff"
                        /> 
                    </div>
                    {this.dashboardRender()}
                </div>
                <footer>
                    <span>Real Estate</span> v1.2.0 © Copyright 2015
                </footer>
			</div>
		)
  	}
}

export default Dashboard;
