import React from 'react';
import StarRatings from 'react-star-ratings';

const Rating = (props) => {

    return (
        <StarRatings
            rating={props.rating}
            starRatedColor="#f9a825"
            numberOfStars={5}
            name='rating'
            starDimension={props.dimension}
            starSpacing={props.spacing}
        />
      );
}

export default Rating;